@import "utils";
@import "fonts";
@import "typography";

body {
  margin: 0;
  padding: 0;
}

.header__top {
  display: flex;
}

.square {
  height: 848px;
  width: 67%;
  background: #56ccf2;
}

.rectangle {
  border: 400px solid transparent;
  border-left: 400px solid #56ccf2;
  border-top: 848px solid #56ccf2;
}

.header__data {
  display: flex;
  width: 80%;
  margin-right: 0;
  padding-left: 18%;
  padding-top: 50px;
}

.header__menu {
  padding-left: 10px;
  margin-top: 5px;
  margin-right: -120px;
  position: relative;
  left: 45%;
}

.header__menu li {
  display: inline-block;
  color: white;
  margin-left: 40px;
}

.header__menu li:nth-child(1) {
  margin-left: 0;
}

.hearUs {
  display: block;
  color: white;
  text-decoration: none;
  position: relative;
  left: 150%;
  top: 2%;
  margin-top: -50px;
}

.header__center {
  margin: 0 auto;
  position: relative;
  left: 50%;
  padding-top: 100px;
}

.header__bottom {
  display: flex;
  margin: 0 auto;
  margin-top: -640px;
  width: 90%;
}

.left__button {
  position: absolute;
  margin-top: 100px;
}

.rightText span {
  display: block;
  transform: rotate(90deg);
  margin-left: 20px;
  margin-top: 180px;
}

.content-top {
  width: 45%;
  margin: 0 auto;
  padding-top: 100px;
}

.left-inner {
  width: 21px;
  height: 21px;
  border: 1px solid #56ccf2;

}

.inner-top {
  display: flex;
}

.inner-top span:last-child {
  margin-left: 20px;
}

.content-center h3 {
  text-align: center;
}

.content-center {
  height: 470px;
}

.content-grid {
  display: grid;
  grid-template-columns: 300px 300px 300px;
  grid-template-rows: 265px;
  width: 70%;
  margin: 0 auto;
}

.paraghraf-content {
  font-weight: bold;
  padding-right: 5px;
  margin-top: -20px;
}

.content-grid > div {
  margin-left: 80px;
}

.content-bottom {
  display: flex;
}

.content-bottom {
  display: flex;
  width: 70%;
  margin: 0 auto;
}

.content-bottom-left {
  width: 40%;
}

.bold-text-left {
  font-weight: bold;
}

.right-inner-bottom {
  display: flex;
}

.top-first,
.top-second,
.top-first-right,
.top-second-right {
  border: 1px solid green;
  width: 255px;
  height: 255px;
}

.parent-first {
  padding-top: 150px;
}

.parent-second {
  padding-top: 100px;
}

.top-second {
  margin-top: 120px;
}

.parent-second {
  padding-left: 30px;
}

.top-second-right {
  margin-top: 100px;
}

.illusion {
  background: #56ccf2;
  height: 976px;
  width: 100%;
}
